<template>
  <div>
    <el-row>
      <el-col :span="24">
        <el-card>
          <div slot="header">日志查询</div>
          <el-header>
            <el-select filterable placeholder="请选择" v-model="selected">
              <el-option
                :key="item.value"
                :label="item.label"
                v-bind:value="item.value"
                v-for="item in options"
              ></el-option>
            </el-select>
            <div class="block" style="display: inline-block">
              <el-date-picker
                :default-time="['00:00:00', '23:59:59']"
                end-placeholder="结束日期"
                start-placeholder="开始日期"
                type="daterange"
                v-model="value"
                value-format="yyyy-MM-dd HH:mm:ss"
              ></el-date-picker>
            </div>
            <el-button @click="search()" type="primary">搜索</el-button>
            <el-select filterable placeholder="请选择部门" v-model="selectDepartment">
              <el-option :key="d.value" :label="d.label" :value="d.value" v-for="d in departments"></el-option>
            </el-select>
            <el-button @click="exportExcel" type="primary">导出上月日志表</el-button>
          </el-header>
          <el-main>
            <el-table :data="tableData" style="width: 100%">
              <el-table-column label="姓名" prop="name" width="180"></el-table-column>
              <el-table-column label="总篇数" prop="count" width="180"></el-table-column>
              <el-table-column label="平均分" prop="avg"></el-table-column>
            </el-table>
            <el-table
              :data="detail"
              @row-click="getDiaLog"
              max-height="600"
              stripe
              style="width:100%; margin-top:10px;"
            >
              <el-table-column label="日期" width="200">
                <template slot-scope="scope">{{ trimTimeStr(scope.row.date) }}</template>
              </el-table-column>
              <el-table-column label="上级点评" prop="score" width="180"></el-table-column>
              <el-table-column label="工作能力分" prop="abilityScore"></el-table-column>
            </el-table>
            <!--日志详情dialog弹出层-->
            <el-dialog
              :modal-append-to-body="false"
              :visible.sync="dialogVisible"
              title="日志详情"
              width="650px"
            >
              <div style="width: 600px;height: 500px;">
                <div class="header" style>
                  <!--头部-->
                  <div class="person_info">
                    <div style="width: 50px;height: 50px; margin-top: 10px;margin-left: 10px;">
                      <img
                        :src="logData.headImage"
                        style="  border-radius:20px; width:50px; height: 50px; display: inline "
                      />
                    </div>
                    <div
                      style="font-size: 16px;color: #70738A;margin-left: 10px;  "
                    >{{logData.name}}</div>
                    <div style="color: #469789 ;">{{logData.attendanceState==0?'非正常出勤':'正常出勤'}}</div>
                    <div
                      style="float: right; margin-right: 25px; margin-top:10px;font-size: 12px; color: #bbbbbb"
                    >工作能力分</div>
                    <br />
                    <div style="font-size: 10px; color: #bbbbbb; margin-left: 25px;">
                      {{trimTimeStr
                      (submissionTime)
                      }}
                    </div>
                    <div
                      style="float: right; margin-right: 50px; font-size: 24px;color: #ee7f3d "
                    >{{abilityScore}}</div>
                    <br />
                  </div>
                  <div class="comment">
                    <div style="margin: 10px 20px;font-size: 14px;color: #70738a">点评人: {{manager}}</div>
                    <div>打星情况;共{{logData.score}}颗星</div>
                  </div>
                </div>
                <div class="summary">
                  <div
                    style="font-size: 20px; color:#70738a;background: #fafafa; margin-left: 0px; margin-bottom: 5px; "
                  >&nbsp;&nbsp;&nbsp;今日工作总结</div>
                  <div class="am">
                    <span style="font-size: 16px; color: #70738a">上午</span>
                    <div v-for="amPlan in am">{{amPlan.content}}</div>
                    <!--<audio v-for="amPlan in am"  src="{{amPlan.voiceUrl}}"> </audio>-->
                    <maudio :key="amVoice.index" :src="amVoice.voiceUrl" v-for="amVoice in am" v-show="amVoice.showType===1"></maudio>
                    <!--语音日志已支持-->
                  </div>
                  <div class="pm">
                    <span style="font-size: 16px; color: #70738a">下午</span>
                    <div v-for="pmPlan in pm">{{pmPlan.content}}</div>
                    <maudio :key="pmVoice.index" :src="pmVoice.voiceUrl" v-for="pmVoice in pm" v-show="pmVoice.showType===1"</maudio>
                  </div>
                  <!--总结-->
                </div>
                <div class="plan">
                  <!--计划-->
                  <div
                    style="font-size: 20px; color:#70738a;background: #fafafa; margin-left: 0px;  margin-bottom: 5px;"
                  >&nbsp;&nbsp;&nbsp;明日工作计划</div>
                  <div>
                    <div v-for="p in plan">{{p.content}}</div>
                    <maudio :key="p.index" :src="p.voiceUrl" v-for="p in plan" v-show="p.showType===1"></maudio>
                  </div>
                </div>
                <div class="experience">
                  <!--心得体会-->
                  <div
                    style="font-size: 20px; color:#70738a;background: #fafafa; margin-left: 0px;  margin-bottom: 5px;"
                  >&nbsp;&nbsp;&nbsp;心得体会</div>
                  <div>
                    <div v-for=" e in experience">{{e.content}}</div>
                    <maudio :key="e.index" :src="e.voiceUrl" v-for=" e in experience" v-show="e.showType===1"></maudio>
                  </div>
                </div>
                <div class="plan_img">
                  <!--图片部分-->
                  <el-image
                    :key="p.index"
                    :src="p.imageUrl"
                    fill="fill"
                    v-for="p in pm"
                    v-show="p.imageUrl"
                  ></el-image>
                </div>
              </div>
            </el-dialog>
            <!-- 日志详情弹出层结束 -->
          </el-main>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import audio from '../home/audio'

export default {
  components: {
    maudio: audio
  },
  data () {
    return {
      img: 'https://wx.qlogo.cn/mmopen/vi_32/Q0j4TwGTfTKe2HwTYcQ0hyeINUISeCMg06rN2RTE7HzWyNwjSEFVTVaC4QlPLXTctZFibNGZHpUkL7kRLjJjNCA/132',
      tableData: '',
      options: '',
      selected: '',
      value: '',
      flag: false,
      dialogVisible: false,
      rowDetail: '',
      detail: [],
      logData: '',
      manager: '',
      abilityScore: '',
      am: '',
      pm: '',
      plan: '',
      experience: '',
      comment: '',
      submissionTime: '',
      selectDepartment: null,
      url: 'http://192.168.0.115:8081'
    }
  },
  created () {
    this.getManagers();
    this.getDepartments();
  },
  methods: {
    exportExcel () {
      var _this = this;
      if (this.selectDepartment) {
        // _this.getRequest(
        //   _this.url + '/excel/score?departmentId=' + this.selectDepartment
        // ).then(res => {
        //   _this.$message({
        //     showClose: true,
        //     message: res.data.msg,
        //     type: 'info'
        //   });
        // });
        window.open(_this.url + '/excel/score?departmentId=' + this.selectDepartment, '_blank');
      } else {
        _this.$message({
          showClose: true,
          message: '请先选择部门',
          type: 'error'
        });
      }
    },
    getDiaLog: function (row) {
      var _this = this;
      this.rowDetail = row;
      // param.append('userId', this.selected);
      // param.append('planId', this.rowDetail.id);
      this.getRequest('/plan/getDetail?userId=' + this.selected + '&planId=' + row.id
      ).then((response) => {
        this.dialogVisible = true;
        let data = response.data;
        this.logData = data;
        this.am = data.am;
        this.pm = data.pm;
        this.plan = data.workPlan;
        this.experience = data.workExperience;
        this.manager = data.manager;
        this.comment = data.comment;
        this.abilityScore = data.abilityScore;
        this.submissionTime = data.submissionTime;
      }).catch(function () {
        _this.$message({
          showClose: true,
          message: '网络异常',
          type: 'error'
        });
      });
    },
    search: function () {
      var _this = this;
      // 检查是否为空
      if (_this.selected != null || _this.value != null) {
        // 后台接口返回这样的数据即可
        _this.getRequest(
           '/getPlan?userId=' + this.selected + '&start=' + this.value[0] + '&end=' + this.value[1]
        ).then((response) => {
          _this.flag = true;
          _this.tableData = response.data.tableData;
          _this.detail = response.data.detail;
          if (response.data.msg != null) {
            _this.$message({
              showClose: true,
              message: response.data.msg,
              type: 'error'
            });
          }
        })
          .catch(function () {
            _this.$message({
              showClose: true,
              message: '网络异常',
              type: 'error'
            });
          });
      } else {
        _this.$message({
          showClose: true,
          message: '请输入要搜索的用户和时间范围',
          type: 'error'
        });
      }
    },
    // 获取负责人
    getManagers: function () {
      var _this = this;
      this.getRequest('/employeeInfo').then((response) => {
        this.options = response.data;
      }).catch(function () {
        _this.$message({
          showClose: true,
          message: '获取负责人列表失败',
          type: 'error'
        });
      })
    },
    getDepartments: function () {
      var _this = this;
      this.getRequest('/department/all').then(res => {
        this.departments = res.data;
      }).catch(function () {
        _this.$message({
          showClose: true,
          message: '获取部门列表失败',
          type: 'error'
        });
      })
    },
    // 格式化时间
    trimTimeStr: function (time) {
      return time.replace('T', ' ');
    }
  }
} 
</script>

<style >
.comment div {
  margin-left: 20px;
  margin-bottom: 10px;
  color: #bbbbbb;
  font-size: 12px;
}
.plan,
.summary,
.experience {
  min-height: 60px;
}
.person_info div {
  display: inline-block;
}
.summary div {
  margin-left: 20px;
  font-size: 16px;
  color: #70738a;
}

.plan div {
  margin-left: 20px;
  font-size: 16px;
  color: #70738a;
}

.experience div {
  margin-left: 20px;
  font-size: 16px;
  color: #70738a;
}

.plan_img img {
  width: 100px;
  height: 100px;
}
</style>


